const _hoisted_4 = /*#__PURE__*/_createStaticVNode("<div class=\"about__text\" data-v-f5b76840><p data-v-f5b76840>Привет! Меня зовут Денис и я front-end разработчик, а это мой блог.</p><p data-v-f5b76840>Мой уютный бложик полностью самописный. Фронт написан на Vue, TypeScript и SCSS, а бэк на Node.JS, Express и TypeScript, база данных - Mongo. Исходных код доступен на github.</p><p data-v-f5b76840>Я безумно обожаю всё, что касается IT. Я долго не мог определится чем конкретно хочу заниматься. А поэтому постигал администрирование Linux, вёл собственные сайты, увлекался компьютерной безопасностью и Android разработкой, учил PHP и SQL, а также С++.</p><p data-v-f5b76840>Придя на фриланс я понял, что по вебу очень много заказов. И только поэтому принял решение развиваться именно в этом направлении. А со временем определился, что конкретно front-end нравится больше. Я безумно люблю пробовать что-то новое из айтишечки.</p><p data-v-f5b76840>А ещё я заядлый линуксоид. На Linux я перешёл примерно в конце 2012 года. Моим первым дистрибутивом был Linux Mint c Cinnamon.</p><br data-v-f5b76840><p data-v-f5b76840><b data-v-f5b76840>Используемая ОС:</b> openSUSE Leap</p><p data-v-f5b76840><b data-v-f5b76840>Используемое DE:</b> KDE Plasma</p><div class=\"about__nav\" data-v-f5b76840><a class=\"btn btn--big btn--yellow\" href=\"https://bit.ly/crynet-resume\" target=\"_blank\" data-v-f5b76840><svg width=\"15\" viewBox=\"0 0 384 512\" data-v-f5b76840><path d=\"M0 64C0 28.65 28.65 0 64 0H224V128C224 145.7 238.3 160 256 160H384V448C384 483.3 355.3 512 320 512H64C28.65 512 0 483.3 0 448V64zM256 128V0L384 128H256z\" data-v-f5b76840></path></svg><span data-v-f5b76840>resume.pdf</span></a><a class=\"btn btn--big\" href=\"https://github.com/uCryNet\" target=\"_blank\" data-v-f5b76840><svg width=\"18\" viewBox=\"0 0 496 512\" data-v-f5b76840><path d=\"M165.9 397.4c0 2-2.3 3.6-5.2 3.6-3.3.3-5.6-1.3-5.6-3.6 0-2 2.3-3.6 5.2-3.6 3-.3 5.6 1.3 5.6 3.6zm-31.1-4.5c-.7 2 1.3 4.3 4.3 4.9 2.6 1 5.6 0 